@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Bungee+Shade&display=swap');

.question {
  background-color: white;
  border: 0.7rem solid black;
  width: auto;
  height: auto;
  text-align: center;
  border-radius: 3rem;
  font-family: 'Bebas Neue';
  width: calc(100%-0.7rem*2);
  opacity: 0.9;

  a {
    color: black;
    text-decoration: underline;
    &:hover {
      color: linear-gradient(to left, violet, indigo, blue, green, yellow, orange, red);
    }
    &:active, &:visited {
      color: black;
    }
  }

  .text2 {
    font-size: 2rem;
    display: block;
    padding: 2rem;
  }

  .text3 {
    font-size: 4rem;
    font-family: 'Bungee Shade';
    font-weight: bold;
  }
  
  .scoreBox {
    padding-bottom: 2rem;
    padding-top: 1rem;
  }

  .contactMeBox {
    padding-top: 1rem;
    font-size: 70%;
  }

  @media only screen and (max-width: 40rem) {
    .text {
      font-size: 2rem;
      padding: 2rem;
    }
  }

  .progress {
    font-size: 0.9rem;
    display: block;
    border: 0.4rem solid black;
    background-color: black;
    color: white;
    border-radius: 3rem;
    margin-bottom: 0;
    border-bottom: none;
    border-end-start-radius: 0;
    border-end-end-radius: 0;
    min-width: 30%;
    max-width: 50%;
    width: 50%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: -1px;
  }

  .text {
    font-size: 3rem;
    display: block;
    padding: 4rem;
  }
}

.button {
  opacity: 0.9;
  border: 0.7rem solid black;
  margin: 0.3rem 0 0 0;
  padding: 1rem;
  border-radius: 2.5rem;
  font-size: 2.4rem;
  text-align: center;
  user-select: none;
  cursor: pointer;
  font-family: 'Bungee Shade';
  width: calc(100%-2rem*2);
  min-width: 15rem;
  
  @media only screen and (max-width: 40rem) {
      min-width: 3rem;
      font-size: 1.6rem;
  }

  &[role="gay"],  &[role="start"] {
    background-image: linear-gradient(to left, violet, indigo, blue, green, yellow, orange, red);
    color: white;
    text-shadow: 0 0 3px #FF0000, 0 0 5px #0000FF;
    font-weight: bold;
    &:hover {
      transform: scale(1.01);
      border-color: white;
    }
    &:active {
      background-image: none;
      background-color: white;
      transform: scale(1);
    }
  }

  &[role="not-gay"] {
    background-color: white;
    color: black;
    text-shadow: 0 0 3px purple, 0 0 5px #0000FF;
    font-weight: bold;

    &:hover {
      border-color: purple;
      transform: scale(1.01);
    }
    &:active {
      border-color: white;
      transform: scale(1);
    }
  }
}
